	module.exports = {"data":{"markdownRemark":{"html":"<p>まさか夢終劇体験版、完売するとは思いもよらなかった。体験版Aが250ちょい持って行って100以上余ったから500も持っていけば絶対余り出るだろうと思っていたが、どうやら夏コミと言うものをナメていたようです。買えなかった皆さんにはほんっとゴメンナサイorz (個人的にはヤケクソに大量生産して余りまくるよりもケチケチ作って完売しちゃって買いたいのにお売り出来ないほうが苦痛だったり)  突然ですが<a href=\"/!/thC/\">東方夢終劇</a>体験版B1.01をリリースしました。体験版A(例大祭版)及び体験版B(C70版)のアップデートパッチです。体験版A2(web版)からのアップデートには対応していません。(AからA2にアップデートした方は一旦Aにロールバックしてください)あと、それなりに容量がありますのでKIAIでどうぞ。 また夏コミ当日プリンタ故障につきジャケットを一部の方にしか配布できなかったのでお詫び代わりに各自で印刷できるようジャケット画像を用意いたしました。体験版B1.01パッチに同梱していますのでどうぞ。 どうして夏コミ後って落ち着きたいのに落ち着けないんだろうか。冬コミ申し込みのリミットがやたらシビアだし、夢終劇関係でやらなきゃいけないことが山ほど積んであるし、戦利品や頂き物は早く鑑賞してなるべく積みにしたくないし、仕事あるし。(盆休み？なにそれ食べれるの？)</p>","frontmatter":{"cover":null,"date":"2006-08-13T23:00:00+09:00","strDate":"2006/8/13","title":"夏コミ乙でした"}}},"pathContext":{"path":"/ja/blog/2006/08/13-夏コミ乙でした/","langKey":"ja"}}

